
.Authcontainer {
    text-transform: uppercase;
    display: flex;
    flex-direction: column;
    color: black;
    margin-top: 30px;
    align-items: center;
    justify-content: center;
  }
  
  h1 {
    font-family: "Montserrat", "Arial", sans-serif;
    color: black;
    text-align: center;
    font-size: 1.5rem;
    margin-bottom: 32px;
  }
  
  .form-background {
    background: #fff;
    width: 420px;
    border-radius: 10px;
    padding: 32px;
    -webkit-box-shadow: 0px 15px 88px -37px rgba(0, 0, 0, 0.17);
    -moz-box-shadow: 0px 15px 88px -37px rgba(0, 0, 0, 0.17);
    box-shadow: 0px 15px 88px -37px rgba(0, 0, 0, 0.17);
  }
  
  form {
    text-transform: capitalize;
    font-family: "Lato", "Arial", sans-serif;
    text-align: center;
  }
  
  input {
    width: 100%;
    margin-bottom: 15px;
    border-radius: 30px;
    border: 1px solid #DAE2F1;
    padding: 15px 20px;
    color: #8498AF;
    font-size: .9rem;
    transition: .4s;
  }
  input:last-child {
    margin-bottom: 0;
  }
  input:focus {
    border-color: black;
    outline: none;
  }
  
  [type="submit"] {
    background: #000;
    border-radius: 30px;
    padding: 16px 30px;
    width: 100%;
    color: #fff;
    text-transform: uppercase;
    font-size: .8rem;
    transition: .4s;
    font-family: "Montserrat", "Arial", sans-serif;
    border: none;
    box-shadow: none;
    margin: 0;
  }
  [type="submit"]:hover {
    opacity: 0.9;
    cursor: pointer;
  }
  