
.container {
    margin-left: auto;
    margin-right: auto;
    margin-top: 4rem;
    max-width: 800px;
    display: -webkit-box;
    display: flex;
    flex-wrap: wrap;
    -webkit-box-align: center;
            align-items: center;
    -webkit-box-pack: center;
            justify-content: center;
  }
  
  .col {
    flex-basis: 0;
    -webkit-box-flex: 1;
            flex-grow: 1;
    max-width: 100%;
  }
  
  .shadow--sm {
    box-shadow: 0px 25px 15px -25px #97a3b8;
  }
  .shadow--sm:hover {
    box-shadow: 0px 10px 10px -10px rgba(151, 163, 184, 0.5);
  }
  .shadow--md {
    box-shadow: 0px 25px 25px -25px #97a3b8;
  }
  .shadow--md:hover {
    box-shadow: 0px 10px 20px -10px rgba(151, 163, 184, 0.5);
  }
  .shadow--lg {
    box-shadow: 0px 25px 35px -25px #97a3b8;
  }
  .shadow--lg:hover {
    box-shadow: 0px 10px 30px -10px rgba(151, 163, 184, 0.5);
  }
  .shadow-smooth-sm {
    box-shadow: 0px 5px 4px -5px rgba(151, 163, 184, 0.5), 0px 15px 8px -15px rgba(151, 163, 184, 0.5), 0px 25px 12px -25px rgba(151, 163, 184, 0.5);
  }
  .shadow-smooth-sm:hover {
    box-shadow: 0px 10px 10px -10px rgba(151, 163, 184, 0.5);
  }
  .shadow-smooth-md {
    box-shadow: 0px 5px 6px -5px rgba(151, 163, 184, 0.5), 0px 15px 12px -15px rgba(151, 163, 184, 0.5), 0px 25px 18px -25px rgba(151, 163, 184, 0.5);
  }
  .shadow-smooth-md:hover {
    box-shadow: 0px 10px 20px -10px rgba(151, 163, 184, 0.5);
  }
  .shadow-smooth-lg {
    box-shadow: 0px 5px 8px -5px rgba(151, 163, 184, 0.5), 0px 15px 16px -15px rgba(151, 163, 184, 0.5), 0px 25px 32px -25px rgba(151, 163, 184, 0.5);
  }
  .shadow-smooth-lg:hover {
    box-shadow: 0px 10px 30px -10px rgba(151, 163, 184, 0.5);
  }
  .shadow-long {
    box-shadow: 0px 10px 0px -10px rgba(151, 163, 184, 0.5), 0px 20px 10px -20px rgba(151, 163, 184, 0.5), 0px 30px 20px -25px rgba(151, 163, 184, 0.5), 0px 40px 30px -30px rgba(151, 163, 184, 0.5), 0px 50px 50px -35px rgba(151, 163, 184, 0.5);
  }
  .shadow-long:hover {
    box-shadow: 0px 10px 30px -10px rgba(151, 163, 184, 0.5);
  }
  
  .card {
    background: #ffffff;
    width: 500px;
    height: 320px;
    border-radius: 1rem;
    margin: 1rem auto;
    -webkit-transition: all .2s ease-in;
    transition: all .2s ease-in;
    display: -webkit-box;
    display: flex;
    -webkit-box-pack: center;
            justify-content: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
            flex-direction: column;
    text-align: center;
  }
  .card Button {
      width: 100px;
      
  }
  .card--text {
    font-size: 18px;
    font-weight: 700;
    color: #000000;
    margin: 0;
    line-height: 50px;
    padding: 0 20px;
  }
  .card--size-state {
    font-size: 36px;
  }
  .card:hover {
    -webkit-transform: scale(0.97);
            transform: scale(0.97);
            text-decoration: wavy;
            cursor: pointer;
  }
